import {Injectable} from '@angular/core';
// @ts-ignore
import {Food} from '../models/food.model';
import {HttpClient} from '@angular/common/http';
import {Observable,} from 'rxjs';
import {concatMap, filter} from 'rxjs/operators';
import {Category} from '../models/category.model';

//const baseUrl = 'http://192.168.0.246:8080/api/client/categories';
const baseUrl = 'https://oafish-orange-production.up.railway.app/api/client/categories';

@Injectable({
  providedIn: 'root',
})
export class CategoryClientService {

  public categories: Category[] = [];
  categorie: Category;
  constructor(private http: HttpClient) {
  }

  getAll(): Observable<Category[]> {
    return this.http.get<Category[]>(baseUrl);
  }

  getCategoryByUserIdAll(id: any): Observable<Category[]> {
    return this.http.get<Category[]>(`${baseUrl}/user/${id}`);
  }

  getCategoriesById(id: number): Observable<Category> {
    // @ts-ignore
    return this.http.get(`${baseUrl}/${id}`);
  }

  delete(id: any): Observable<any> {
    return this.http.delete(`${baseUrl}/${id}`);
  }
  create(data: any): Observable<any> {
    return this.http.post(baseUrl, data);
  }
  update(id: any, data: any): Observable<any> {
    return this.http.put(`${baseUrl}/${id}`, data);
  }

  findByTitle(title: any): Observable<Category[]> {
    return this.http.get<Category[]>(`${baseUrl}?title=${title}`);
  }
}
