import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {Category} from 'src/app/client1/models/category.model';
import {Food} from 'src/app/client1/models/food.model';
import {FoodClientService} from 'src/app/client1/services/food-client.service';
import {CategoryClientService} from '../../../services/category-client.service';
// @ts-ignore
import {TranslateService} from '@ngx-translate/core';
import {CanalService} from '../../../services/canal.service';
import {TokenStorageService} from '../../../_servicesAuth/token-storage.service';

@Component({
  selector: 'app-listing',
  templateUrl: './listing-client-page.component.html',
  styleUrls: ['./listing-client-page.component.scss'],
})
export class ListingClientPage implements OnInit {
  categories: Category[] = [];
  foods: Food[] = [];
  title = '';
  all = 'All';
  canal: any;
  userId: any;

  constructor(private foodService: FoodClientService,
              private categoryService: CategoryClientService,
              private router: Router,
              private translate: TranslateService,
              private canalService: CanalService,
              private token: TokenStorageService,
  ) {
    translate.setDefaultLang('en');
  }

  ngOnInit() {
    this.getParamsURL();
    this.getAllCategories();
    this.getAllPublishedFoods();

  }


  getParamsURL(){
    // Obtenir l'URL complète
    const url = window.location.href;
// Obtenir les paramètres de l'URL
    const params = new URLSearchParams(new URL(url).search);
// Obtenir la valeur du paramètre 'param'
    this.canal = params.get('canal');
    this.userId = params.get('user');
    console.log('valeur de canal: '+this.canal); // Affiche 'valeur'
    console.log('valeur de userid : '+this.userId); // Affiche 'valeur'
    this.canalService.setCanal(this.canal);
    this.canalService.setUserId(this.userId);
  }
  getAllPublishedFoods(): void {
    this.foodService.getAllFoodUserPublished(this.userId)
      .subscribe(
        data => {
          this.foods = data;
          console.log(data);
        },
        error => {
          console.log(error);
        });
  }

  getAllCategories() {
    this.categoryService.getCategoryByUserIdAll(this.canalService.getUserId())
      .subscribe(
        data => {
          this.categories = data;
          console.log('Categories: ', data);
        },
        error => {
          console.log(error);
        });
  }

  goToDetailPage(id: number) {
    //this.router.navigate(['/client1/client/detail', id]);
    this.router.navigate(['/client/list-sub-food', id]);
  }

  searchTitle(): void {
    this.foodService.findByTitle(this.userId,this.title)
      .subscribe(
        data => {
          this.foods = data;
          console.log(data);
        },
        error => {
          console.log(error);
        });
  }

  test(): void {
    console.log('test btn');
  }


  searchAllByCategory(){
    this.getAllPublishedFoods();
  }
  async searchByCategory(title: string) {
        this.foodService.findByTitle(this.userId,title)
          .subscribe(
            data => {
              this.foods = data;
              console.log(data);
            },
            error => {
              console.log(error);
            });
  }
  onLanguageChange(event) {
    this.translate.use(event.detail.value);
    //console.log(event.detail.value); // the value of the selected option
  }
}
